import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Offcanvas from 'react-bootstrap/Offcanvas';
import { useNavigate } from 'react-router-dom';
import { context } from "./ThemeContext"
import sun from "./images/icon-sun.svg"
import moon from "./images/icon-moon.svg"
import { useContext } from 'react';
import '../styles/global.css';

import backgroundLight from './images/bg-desktop-light.jpg'
import backgroundDark from './images/bg-desktop-dark.jpg'
function Menu() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const navigate = useNavigate()
  const { theme, setTheme } = useContext(context)
  function close() {
    navigate('/History')
    return handleClose()
  }
  const handletheme = () => {
    setTheme(!theme)
    return handleClose()
  }
  const MainMenu = () => {
    navigate('/')
    return handleClose()
  }
  return (
    <>

      <Button style={{ backgroundColor: theme ? 'hsl(236, 33%, 92%)' : 'hsl(235, 24%, 19%)', color: theme ? "hsl(236, 9%, 61%)" : 'white', borderColor: 'none', outlineColor: 'none' }} onClick={handleShow}>
        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-list" viewBox="0 0 16 16">
          <path fill-rule="evenodd" d="M2.5 12a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5m0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5m0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5" />
        </svg>
      </Button>

      <Offcanvas show={show} variant="secondary" onHide={handleClose} style={{ fontFamily: 'Josefin Sans, sans-serif', backgroundColor: theme ? 'hsl(236, 33%, 92%)' : 'hsl(235, 24%, 19%)', color: theme ? "hsl(236, 9%, 61%)" : 'white', width: '250px' }}>
        <Offcanvas.Header
          closeButton
          style={{ color: theme ? "hsl(236, 9%, 61%)" : 'white' }}>
          <Offcanvas.Title style={{ cursor: 'pointer' }} onClick={MainMenu}>Duplicator</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
          <div className="thme" onClick={handletheme} style={{ cursor: 'pointer' }}>
            <h3 className="nav">{theme ? "Light mode" : "Dark mode"}</h3>
            <img src={theme ? sun : moon} alt="theme" />
          </div>
          <div>
            <br />

            <h5 className="buttons" onClick={close}>
              View History
              &nbsp;
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-list-columns" viewBox="0 0 16 16">
                <path fill-rule="evenodd" fill='' d="M0 .5A.5.5 0 0 1 .5 0h9a.5.5 0 0 1 0 1h-9A.5.5 0 0 1 0 .5m13 0a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 0 1h-2a.5.5 0 0 1-.5-.5m-13 2A.5.5 0 0 1 .5 2h8a.5.5 0 0 1 0 1h-8a.5.5 0 0 1-.5-.5m13 0a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 0 1h-2a.5.5 0 0 1-.5-.5m-13 2A.5.5 0 0 1 .5 4h10a.5.5 0 0 1 0 1H.5a.5.5 0 0 1-.5-.5m13 0a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 0 1h-2a.5.5 0 0 1-.5-.5m-13 2A.5.5 0 0 1 .5 6h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5m13 0a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 0 1h-2a.5.5 0 0 1-.5-.5m-13 2A.5.5 0 0 1 .5 8h7a.5.5 0 0 1 0 1h-7a.5.5 0 0 1-.5-.5m13 0a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 0 1h-2a.5.5 0 0 1-.5-.5m-13 2a.5.5 0 0 1 .5-.5h9a.5.5 0 0 1 0 1h-9a.5.5 0 0 1-.5-.5m13 0a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 0 1h-2a.5.5 0 0 1-.5-.5m-13 2a.5.5 0 0 1 .5-.5h5a.5.5 0 0 1 0 1h-5a.5.5 0 0 1-.5-.5m13 0a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 0 1h-2a.5.5 0 0 1-.5-.5m-13 2a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H.5a.5.5 0 0 1-.5-.5m13 0a.5.5 0 0 1 .5-.5h2a.5.5 0 0 1 0 1h-2a.5.5 0 0 1-.5-.5" />
              </svg></h5>
          </div>
        </Offcanvas.Body>
      </Offcanvas>
    </>
  );
}

export default Menu;