import { useContext, useEffect, useState } from "react"
import { NavLink, useNavigate } from "react-router-dom"
import sun from "./images/icon-sun.svg"
import moon from "./images/icon-moon.svg"
import { context } from "./ThemeContext"
import backgroundLight from './images/bg-desktop-light.jpg'
import backgroundDark from './images/bg-desktop-dark.jpg'
import Menu from "./Menu"
function Navbar() {
  const [width, setWidth] = useState(window.innerWidth)
  const { theme, setTheme } = useContext(context)
  const navigate = useNavigate()
  const style = {
    backgroundImage: `url(${theme ? backgroundLight : backgroundDark})`,
    color: "white",
  }
  const handleResize = () => {
    setWidth(window.innerWidth);
  };

  useEffect(() => {
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };

  }, []);
  console.log(width)
  return (
    <div className="theme" style={style}>
      <div>
        <h3 className="nav" >Duplicate Removal</h3>
      </div>
      <div>
        <Menu />
      </div>
    </div>
  )
}
export default Navbar