import { useEffect, useState } from "react"
import { context } from "./ThemeContext"
import { useContext } from "react"
import Modals from "./Modals";
import { Navigate, useNavigate } from "react-router-dom";
import { Modal } from "bootstrap";
function Home() {
  const [input, setInput] = useState("")
  const message = document.getElementById("error")
  const [errormsg, setErrormsg] = useState('')
  const navigate = useNavigate()
  const [show, setShow] = useState(false);
  const [allInputs, setAllInputs] = useState(() => {
    const storedData = localStorage.getItem('allInputs')
    return storedData ? JSON.parse(storedData) : []
  })
  const handleClose = () => setShow(false);
  function validate(event) {
    const value = event.target.value;
    setInput(value)
    if (!isAlphabet(value)) {
      setErrormsg("Input should not contain numbers")
    }
    else if (isAlphabet(value)) {
      setErrormsg("")
    }
  }
  function isAlphabet(value) {
    const regex = /^[a-zA-z\s]*$/
    return regex.test(value)
  }
  function handleSubmit(event) {
    if (!isAlphabet(input)) {
      setErrormsg("Input should not contain numbers")
      event.preventDefault()
      setShow(true)
    }
    else if (isAlphabet(input)) {
      localStorage.setItem("arrInput", JSON.stringify(Array.from(input)));
      const updatedInputs = [...allInputs, input];
      setAllInputs(updatedInputs);
      localStorage.setItem("allInputs", JSON.stringify(updatedInputs)); // Store in local storage
      navigate('/Remove', { state: { input } })
    }
  }
  useEffect(() => {
    localStorage.setItem("allInputs", JSON.stringify(allInputs))
  }, [allInputs])
  const { theme, setTheme } = useContext(context)


  const style1 = {
    backgroundColor: theme ? 'hsl(0, 0%, 98%)' : 'hsl(235, 24%, 19%)'
  }
  const style2 = {
    backgroundColor: theme ? 'hsl(236, 33%, 92%)' : 'hsl(235, 21%, 11%)',
    color: "white",
    width: "100.4%",

  }

  return (
    <div className="form" style={style2}>

      <form onSubmit={handleSubmit} style={style1}>
        <input type="text" placeholder="Enter word..." onInput={validate} value={input} style={{ backgroundColor: !theme && 'hsl(235, 19%, 35%)', paddingLeft: '30px', color: !theme && 'white' }} required />
        <span style={{ color: 'red' }} id="error">{errormsg}</span>
        <input id="button" style={{ ...style1, color: theme ? 'hsl(235, 19%, 35%)' : 'hsl(233, 11%, 84%' }} type="submit" value="Submit" />
      </form>
      <>

        <Modals showk={show} handleClose={handleClose} msg="Input should not contain number" />

      </>
    </div>
  )
}
export default Home